import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta, IArgsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Switch extends DefaultObject<{}, {}, any[], [any], [number], {}> {
    static description = "Route multiple inlets to a single outlet";
    static inlets: IInletsMeta = [
        {
            isHot: false,
            type: "number",
            description: "Which inlet to route to the output"
        }
    ];
    static outlets: IOutletsMeta = [
        {
            type: "anything",
            description: "The value from the selected inlet"
        }
    ];
    static args: IArgsMeta = [{
        type: "number",
        optional: false,
        default: 2,
        description: "The number of inlets to select from"
    }];
    _ = { selection: 0, data: 0 };
    handleUpdateArgs(args: any[]) {
        if (args.length !== 1) {
            this.error("Inlet count required");
            return;
        }

        const inlets = +args[0];
        this.inlets = inlets + 1;

        this.setMeta({
            inlets: [
                {
                    isHot: false,
                    type: "number",
                    description: "Which inlet to route to the output"
                },
                ...new Array(inlets).fill({
                    isHot: true,
                    type: "anything",
                    description: "The value to output on the first outlet"
                })
            ]
        })
    }
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.handleUpdateArgs(this.args);
            this.outlets = 1;
        });
        this.on("updateArgs", (args) => {
            this.handleUpdateArgs(args);
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    const clamped = Math.min(Math.abs(Math.floor(+data)), this.inlets - 1);
                    this._.selection = clamped;
                }
            } else {
                if (inlet === this._.selection) {
                    this.outlet(0, data);
                }
            }
        });
    }
}
