import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IArgsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Gate extends DefaultObject<{}, {}, any[], [any], [number], {}> {
    static description = "Route an inlet to multiple outlets";
    static inlets: IInletsMeta = [
        {
            isHot: false,
            type: "number",
            description: "Which outlet to send the input to"
        },
        {
            isHot: true,
            type: "anything",
            description: "The value to send out selected outlet"
        }
    ];
    static args: IArgsMeta = [{
        type: "number",
        optional: false,
        default: 2,
        description: "The number of outlets to select from"
    }];
    _ = { selection: 0, data: 0 };
    handleUpdateArgs(args: any[]) {
        if (args.length !== 1) {
            this.error("Outlet count required");
            return;
        }

        const outlets = +args[0];
        this.outlets = outlets;

        this.setMeta({
            outlets: [
                ...new Array(outlets).fill({
                    type: "anything",
                    description: "The value in the right inlet if the index matches"
                })
            ]
        })
    }
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.handleUpdateArgs(this.args);
            this.inlets = 2;
        });
        this.on("updateArgs", (args) => {
            this.handleUpdateArgs(args);
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    const clamped = Math.min(Math.abs(Math.floor(+data)), this.inlets - 1);
                    this._.selection = clamped;
                }
            } else if (inlet === 1) {
                if (this._.selection != 0) {
                    this.outlet(this._.selection - 1, data);
                }
            }
        });
    }
}
