import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Powtodb extends DefaultObject<{}, {}, [number], [number], [], {}> {
    static package = "electrosmith";
    static author = "btice";
    static version = "1.0";
    static description = "Convert power units (equal to square of RMS Amplitude), to decibels";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "number",
            description: "Power"
        }
    ];
    static outlets: IOutletsMeta = [{
        type: "number",
        description: "Decibels"
    }];
    static docs: string = "utilities/docs/powtodb.html";
    _ = { decibels: undefined as number };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {

                        if (data <= 0) {
                            this._.decibels = 0.;
                        }

                        else {

                            // the natural log of 10, ln(10),  is approximately 2.302585092994046f
                            this._.decibels = 100. + 10. / 2.302585092994046 * Math.log(data);

                            if (this._.decibels < 0.) {
                                this._.decibels = 0.;
                            }
                        }

                    } catch (e) {
                        this.error(e);
                        return;
                    }
                }
                this.outlet(0, this._.decibels);
            }
        });
    }
}
