import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Dbtorms extends DefaultObject<{}, {}, [number], [number], [], {}> {
    static package = "electrosmith";
    static author = "btice";
    static version = "1.0";
    static description = "Convert decibels to power units (equal to the square of RMS Amplitude)";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "number",
            description: "Decibels"
        }
    ];
    static outlets: IOutletsMeta = [{
        type: "number",
        description: "RMS Amplitude"
    }];
    static docs: string = "utilities/docs/dbtorms.html";
    _ = { rms: undefined as number };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {

                        if (data <= 0) {
                            this._.rms = 0.;
                        }

                        else {

                            if (data > 485.) data = 485.;

                            // the natural log of 10, ln(10),  is approximately 2.302585092994046f
                            this._.rms = Math.exp((2.302585092994046 * 0.05) * (data - 100.));
                        }

                    } catch (e) {
                        this.error(e);
                        return;
                    }
                }
                this.outlet(0, this._.rms);
            }
        });
    }
}
