import { Bang, DefaultObject } from "../../sdk";
import type { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Loadmess extends DefaultObject<{}, {}, [Bang], [Bang]> {
    static description = "Output the given message on patcher load.";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "Trigger the message output."
    }];
    static outlets: IOutletsMeta = [{
        type: "anything",
        description: "Sent out on patcher load or inlet."
    }];
    static args: IArgsMeta = [{
        type: "anything",
        optional: false,
        description: "Message to output on patcher load."
    }]
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;

            if (this.args.length < 1) {
                this.error("No message to output on patcher load.");
                return;
            }

            this.patcher.on("postInited", () => this.outlet(0, this.args[0]));
        });
        this.on("inlet", ({ inlet }) => {
            if (inlet === 0) {
                if (this.args.length < 1) {
                    this.error("No message to output on patcher load.");
                    return;
                }
                this.outlet(0, this.args[0]);
            }
        });
    }
}
