import { Bang, DefaultObject } from "../../sdk";
import type { IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Loadbang extends DefaultObject<{}, {}, [Bang], [Bang]> {
    static description = "Bang on patcher load.";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "Anything to transform to a bang."
    }];
    static outlets: IOutletsMeta = [{
        type: "bang",
        description: "Bang on patcher load or inlet."
    }];
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;
            this.patcher.on("postInited", () => this.outlet(0, new Bang()));
        });
        this.on("inlet", ({ inlet }) => {
            if (inlet === 0)
                this.outlet(0, new Bang());
        });
    }
}
