import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Ftom extends DefaultObject<{}, {}, [number], [number], [], {}> {
    static package = "electrosmith";
    static author = "btice";
    static version = "1.0";
    static description = "Convert frequency in Hz to midi note number";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "number",
            description: "Frequency"
        }
    ];
    static outlets: IOutletsMeta = [{
        type: "number",
        description: "Midi Note Number"
    }];
    static docs: string = "utilities/docs/ftom.html";
    _ = { note: undefined as number };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {

                        if (data <= 0) {
                            this._.note = -1500;
                        }

                        else {
                            this._.note = 12. * Math.log(data / 220.) / Math.log(2.) + 57.01;
                        }

                    } catch (e) {
                        this.error(e);
                        return;
                    }
                }
                this.outlet(0, this._.note);
            }
        });
    }
}
