import { DefaultObject, isBang } from "../../sdk";
import type { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";
import { scale } from "../../../../../common/web/scaleFunction";

export default class ScaleClamped extends DefaultObject<{}, {}, [number, number, number, number], [number], [number, number, number, number], {}> {
    static package = "electrosmith";
    static author = "Corvus Prudens";
    static version = "1.0";
    static description = "Scale a number from an input range to an output range with clamping";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "number",
            description: "Number to scale"
        },
        {
            isHot: false,
            type: "number",
            description: "Input range minimum"
        },
        {
            isHot: false,
            type: "number",
            description: "Input range maximum"
        },
        {
            isHot: false,
            type: "number",
            description: "Output range minimum"
        },
        {
            isHot: false,
            type: "number",
            description: "Output range maximum"
        }
    ];
    static outlets: IOutletsMeta = [{
        type: "number",
        description: "Scaled number"
    }];
    static args: IArgsMeta = [
        {
            optional: true,
            type: "number",
            description: "Input range minimum"
        },
        {
            optional: true,
            type: "number",
            description: "Input range maximum"
        },
        {
            optional: true,
            type: "number",
            description: "Output range minimum"
        },
        {
            optional: true,
            type: "number",
            description: "Output range maximum"
        }
    ];
    // static docs: string = "utilities/docs/mtof.html";

    updateRange(args: number[]) {
        this._.inputLow = args[0] || 0;
        this._.inputHigh = args[1] || 0;
        this._.outputLow = args[2] || 0;
        this._.outputHigh = args[3] || 0;
    }

    _ = { output: undefined as number, inputLow: undefined as number, inputHigh: undefined as number, outputLow: undefined as number, outputHigh: undefined as number };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 5;
            this.outlets = 1;
        });
        this.on("postInit", () => {
            this.updateRange(this.args);
        });
        this.on("argsUpdated", ({ args }) => {
            this.updateRange(args);
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {
                        const clamped = Math.min(Math.max(data, this._.inputLow), this._.inputHigh);
                        this._.output = scale(clamped, this._.inputLow, this._.inputHigh, this._.outputLow, this._.outputHigh);
                    } catch (e) {
                        this.error(e);
                        return;
                    }
                }
                this.outlet(0, this._.output);
            } else if (inlet === 1) {
                if (!isBang(data)) {
                    this._.inputLow = +data;
                }
            } else if (inlet === 2) {
                if (!isBang(data)) {
                    this._.inputHigh = +data;
                }
            } else if (inlet === 3) {
                if (!isBang(data)) {
                    this._.outputLow = +data;
                }
            } else if (inlet === 4) {
                if (!isBang(data)) {
                    this._.outputHigh = +data;
                }
            }
        });
    }
}
