import { Message } from "@jspatcher/jspatcher/src/core/message";
import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta, IArgsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export class Append extends DefaultObject<{}, {}, [any], [any], [], {}> {
    static description = "Append a value to a message";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "The input message"
    },
    {
        isHot: false,
        type: "anything",
        description: "The item to append"
    }];
    static outlets: IOutletsMeta = [{
        type: "anything",
        description: "The resulting message"
    }];
    static args: IArgsMeta = [{
        type: "anything",
        optional: true,
        description: "The item to append"
    }];
    _ = { result: undefined as Message, appender: undefined as any };
    updateAppender(args: any[]) {
        if (args.length == 0) {
            this._.appender = undefined;
        } else {
            this._.appender = args[0];
        }
    }
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 2;
            this.outlets = 1;
            this.updateAppender(this.args);
        });
        this.on("updateArgs", (args) => {
            this.updateAppender(args);
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {
                        if (data instanceof Array || data instanceof Message) {
                            this._.result = Message.from(data);
                        } else {
                            this._.result = Message.from([data]);
                        }

                        if (this._.appender instanceof Array || this._.appender instanceof Message) {
                            this._.result = Message.from(this._.result.concat(this._.appender));
                        } else {
                            this._.result.push(this._.appender);
                        }

                        this.outlet(0, this._.result);
                    } catch (e) {
                        this.error(e);
                        return;
                    }
                } else {
                    this.outlet(0, this._.result);
                }
            } else if (inlet === 1) {
                this.updateAppender([data]);
            }
        });
    }
}
