import { Bang, DefaultObject, isBang } from "../../sdk";
import type { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Iter extends DefaultObject<{}, {}, [number], [Bang, Bang, number], number[], {}> {
    static package = "electrosmith";
    static author = "Corvus Prudens";
    static version = "1.0";
    static description = "Repeatedly produce bangs until the range is satisfied.";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "bang",
            description: "Start the iteration"
        }
    ];
    static outlets: IOutletsMeta = [
        {
            type: "bang",
            description: "Bang for each step in the range"
        },
        {
            type: "bang",
            description: "Bang when the range is satisfied"
        },
        {
            type: "number",
            description: "Outputs the value at each step in the range"
        }
    ];
    static args: IArgsMeta = [
        {
            type: "number",
            description: "The number of iterations if one argument is provided, or the start of the range",
            optional: false,
        },
        {
            type: "number",
            description: "The end of the range",
            optional: true,
        },
        {
            type: "number",
            description: "The step size",
            optional: true,
        }
    ];
    updateRange(args: number[]) {
        if (args.length === 1) {
            this._.start = 0;
            this._.stop = +args[0];
            this._.step = 1;
        } else if (args.length === 2) {
            this._.start = +args[0];
            this._.stop = +args[1];
            this._.step = 1;
        } else {
            this._.start = +args[0];
            this._.stop = +args[1];
            this._.step = +args[2];
        }
    }
    _ = { start: 0, stop: 0, step: 1 };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 3;
            this.updateRange(this.args);
        });
        this.on("argsUpdated", ({ args }) => {
            this.updateRange(args);
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (this._.start <= this._.stop) {
                    if (this._.step <= 0) {
                        this.error("iteration will never terminate");
                        return;
                    }

                    for (let i = this._.start; i < this._.stop; i += this._.step) {
                        this.outlet(0, new Bang());
                        this.outlet(2, i);
                    }
                    this.outlet(1, new Bang());
                } else if (this._.start > this._.stop) {
                    if (this._.step >= 0) {
                        this.error("iteration will never terminate");
                        return;
                    }

                    for (let i = this._.start; i > this._.stop; i += this._.step) {
                        this.outlet(0, new Bang());
                        this.outlet(2, i);
                    }
                    this.outlet(1, new Bang());
                }
            }
        });
    }
}
