import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";
import { scale } from "../../../../../common/web/scaleFunction";

export default class ScaleClampedAudio extends JsDspProcessor {
    static description: string = "Scale a number from an input range to an output range with clamping"
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "Number to scale"
        },
        {
            isHot: false,
            type: "signal",
            description: "Input range minimum"
        },
        {
            isHot: false,
            type: "signal",
            description: "Input range maximum"
        },
        {
            isHot: false,
            type: "signal",
            description: "Output range minimum"
        },
        {
            isHot: false,
            type: "signal",
            description: "Output range maximum"
        }
    ];
    static outlets: IOutletsMeta = [{
        type: "signal",
        description: "Scaled number"
    }];
    static args: IArgsMeta = [
        {
            optional: true,
            type: "number",
            description: "Input range minimum",
            default: 0
        },
        {
            optional: true,
            type: "number",
            description: "Input range maximum",
            default: 0
        },
        {
            optional: true,
            type: "number",
            description: "Output range minimum",
            default: 0
        },
        {
            optional: true,
            type: "number",
            description: "Output range maximum",
            default: 0
        }
    ];
    static argsOffset: number = 1;
    // static docs: string = "utilities/docs/mtof.html";

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        const inputStream = inputs[0][0];
        const inputLow = inputs[0][1];
        const inputHigh = inputs[0][2];
        const outputLow = inputs[0][3];
        const outputHigh = inputs[0][4];

        const outputStream = outputs[0][0];

        for (let i = 0; i < outputStream.length; i++) {
            const data = inputStream[i];
            const clamped = Math.min(Math.max(data, inputLow[i]), inputHigh[i]);
            outputStream[i] = scale(clamped, inputLow[i], inputHigh[i], outputLow[i], outputHigh[i]);
        }

        return true;
    }
}
