import { Bang, DefaultObject, isBang } from "../../sdk";
import type { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Counter extends DefaultObject<{}, {}, [number], [number, Bang, number], number[], {}> {
    static package = "electrosmith";
    static author = "Corvus Prudens";
    static version = "1.0";
    static description = "Iterates over the given range.";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "bang",
            description: "Advance the range one step"
        }
    ];
    static outlets: IOutletsMeta = [
        {
            type: "number",
            description: "The current step in the range"
        },
        {
            type: "bang",
            description: "Bang when the range is satisfied"
        },
        {
            type: "number",
            description: "Outputs the number of ranges satisfied"
        }
    ];
    static args: IArgsMeta = [
        {
            type: "number",
            description: "The number of iterations if one argument is provided, or the start of the range",
            optional: false,
        },
        {
            type: "number",
            description: "The end of the range",
            optional: true,
        },
        {
            type: "number",
            description: "The step size",
            optional: true,
        }
    ];
    updateRange(args: number[]) {
        if (args.length === 1) {
            this._.start = 0;
            this._.stop = +args[0];
            this._.step = 1;
        } else if (args.length === 2) {
            this._.start = +args[0];
            this._.stop = +args[1];
            this._.step = 1;
        } else {
            this._.start = +args[0];
            this._.stop = +args[1];
            this._.step = +args[2];
        }

        if (this._.start <= this._.stop) {
            if (this._.step <= 0) {
                this.error("iteration will never terminate");
                return;
            }
        } else if (this._.start > this._.stop) {
            if (this._.step >= 0) {
                this.error("iteration will never terminate");
                return;
            }
        }

        this._.current_step = this._.start;
    }
    _ = { start: 0, stop: 0, step: 1, current_step: 0, num_satisfied: 0 };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 3;
            this.updateRange(this.args);
        });
        this.on("argsUpdated", ({ args }) => {
            this.updateRange(args);
        });
        this.on("inlet", ({ inlet }) => {
            if (inlet === 0) {
                this.outlet(0, this._.current_step);

                this._.current_step += this._.step;
                if (this._.start <= this._.stop) {
                    if (this._.current_step + this._.step > this._.stop) {

                        this._.num_satisfied += 1;

                        this.outlet(1, new Bang());
                        this.outlet(2, this._.num_satisfied);
                        this._.current_step = this._.start;
                    }
                } else if (this._.start > this._.stop) {
                    if (this._.current_step + this._.step < this._.stop) {

                        this._.num_satisfied += 1;

                        this.outlet(1, new Bang());
                        this.outlet(2, this._.num_satisfied);
                        this._.current_step = this._.start;
                    }
                }
            }
        });
    }
}
