import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class MtofAudio extends JsDspProcessor {
    static description: string = "Convert midi note number to frequency in Hz"
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "Midi Note Number"
        }
    ];
    static outlets: IOutletsMeta = [{
        type: "signal",
        description: "Frequency"
    }];
    static argsOffset: number = 0;
    static docs: string = "utilities/docs/mtof.html";

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        const frequency = inputs[0][0];
        const outputStream = outputs[0][0];

        for (let i = 0; i < outputStream.length; i++) {
            const data = frequency[i];

            if (data <= -1500.) {
                outputStream[i] = 0.;
            } else if (data > 1499.) {
                outputStream[i] = 440. * Math.exp(0.0577625565 * (1499. - 69.));
            } else {
                outputStream[i] = 440. * Math.exp(0.0577625565 * (data - 69.));
            }
        }

        return true;
    }
}
