import { Message } from "@jspatcher/jspatcher/src/core/message";
import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta, IArgsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export class Prepend extends DefaultObject<{}, {}, [any], [any], [], {}> {
    static description = "Prepend a value to a message";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "The input message"
    },
    {
        isHot: false,
        type: "anything",
        description: "The item to prepend"
    }];
    static outlets: IOutletsMeta = [{
        type: "anything",
        description: "The resulting message"
    }];
    static args: IArgsMeta = [{
        type: "anything",
        optional: true,
        description: "The item to prepend"
    }];
    _ = { result: undefined as Message, prepender: undefined as any };
    updatePrepender(args: any[]) {
        if (args.length == 0) {
            this._.prepender = undefined;
        } else {
            this._.prepender = args[0];
        }
    }
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 2;
            this.outlets = 1;
            this.updatePrepender(this.args);
        });
        this.on("updateArgs", (args) => {
            this.updatePrepender(args);
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {

                        if (this._.prepender) {
                            if (this._.prepender instanceof Array || this._.prepender instanceof Message) {
                                this._.result = Message.from(this._.prepender);
                            } else {
                                this._.result = Message.from([this._.prepender]);
                            }
                        } else {
                            this._.result = new Message();
                        }

                        if (data instanceof Array || data instanceof Message) {
                            this._.result = Message.from(this._.result.concat(data));
                        } else {
                            this._.result.push(data);
                        }

                        this.outlet(0, this._.result);
                    } catch (e) {
                        this.error(e);
                        return;
                    }
                } else {
                    this.outlet(0, this._.result);
                }
            } else if (inlet === 1) {
                this.updatePrepender([data]);
            }
        });
    }
}
